.find-hotel__title {
   margin-top: 10px;
   font-weight: bold;
   font-size: 24px;
   line-height: 29px;
 }
 
 .find-hotel__place,
 .find-hotel__dates,
 .find-hotel__guests,
 .find-hotel__button {
   margin-top: 20px;
 }
 
 .label-text {
   font-weight: bold;
   font-size: 12px;
   line-height: 15px;
   text-transform: uppercase;
   margin-top: 10px;
   margin-bottom: 5px;
 }
 
 #place-country,
 #place-town,
 #guest {
   width: 100%;
   height: 40px;
   border: 1px solid rgba(31, 32, 65, 0.25);
 }
 
 #date-arrival,
 #date-departure {
   width: 180px;
   height: 40px;
   border: 1px solid rgba(31, 32, 65, 0.25);
 }
 
 .find-hotel__button {
   width: 100%;
   height: 40px;
   background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
   border-radius: 22px;
   font-weight: bold;
   font-size: 12px;
   line-height: 15px;
   text-align: center;
   text-transform: uppercase;
   color: #ffffff;
 }