.hotel-card {
  width: 400px;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.1);
  border-radius: 4px;
  padding-bottom: 20px;
  margin-bottom: 25px;
}
.hotel-card-img {
  position: relative;
  width: 100%;
  height: 250px;
  background-image: url(../../../../assets/hotel-example.png);
  background-size: cover;
  margin-bottom: 20px;
  border-radius: 4px 4px 0px 0px;
}
.hotel-card-info {
  padding: 0 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 15px 0;
}
.info-num {
  font-weight: bold;
  font-size: 18px;
  line-height: 17px;
  color: #1f2041;
}
.info-vip {
  font-weight: bold;
  font-size: 14px;
  line-height: 15px;
  text-transform: uppercase;
  color: #bc9cff;
}
.info-cost {
  font-size: 18px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.7);
}
.info-raiting {
  display: flex;
}
.card-info-line {
  width: 87%;
  background: rgba(31, 32, 65, 0.3);
  border: none;
  height: 1px;
  margin: auto;
}
.rating-star {
  margin: 2px;
  width: 25px;
  height: 25px;
  background-image: url(../../../../assets/active-star.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.non-filled {
  background-image: url(../../../../assets/star.png);
}
.info-reviews {
  font-size: 18px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.7);
}
.favorite-icon {
  position: absolute;
  width: 40px;
  height: 40px;
  background-image: url(../../../../assets/not-favorite.png);
  background-repeat: no-repeat;
  background-size: contain;
  right: 10px;
  top: 10px;
  cursor: pointer;
}
.favorite {
  background-image: url(../../../../assets/favorite.png);
}
