.popular-hotel-card{
   width: 24%;
   height: 380px;
}

.hotel-card__img{
   width: 100%;
   height: 240px;
   object-fit: cover;
}

.hotel-card__title-hotel{
   margin-top: 10px;
   font-size: 26px;
}

.hotel-card__title-country{
   margin-bottom: 10px;
   font-size: 18px;
   font-weight: 100;
}

.min-price-hotel{
   font-weight: 700;
}

.hotel-card__rating{
   justify-content: flex-start;
   gap: 10px;
   margin-top: 15px;
}

.hotel-card__rating-grade{
   width: 28px;
   height: 28px;   
   background-color: #8ba4f9;
   border-radius: 5.09091px 5.09091px 5.09091px 0;
}

.hotel-card__rating-grade-text{
  text-align: center;
  margin-top: 4px;
  color: white;
  font-weight: 700;
  font-size: 15px;
}

.hotel-card__rating-status{
   align-self: center;
   font-weight: 700;
}

.hotel-card__rating-comments{
   align-self: center;
   font-size: 12px;
   color: rgb(145, 145, 145);
}
